import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    song     : {
      isFlash : false,
      myAudio : null,
      name    : null,
      url     : null,
      imgUrl  : null,
      id      : null,
      icons   : 'icon-pause'
    },
    // 播放队列
    queue      : [],
    queueIndex : 0,
    lyric      : {
      // 固定歌词 高亮的行数
      C_pos    : 0,

      // 现在高亮的函数
      lineNo   : 0,
    },
    // 首页的海报
    SirenRecom : [],
    
  },
  mutations: {
    // 修改首页find的新歌推荐
    gaiSirenRecom(store,data){
      store.SirenRecom = data;
      console.log('find的新歌推荐',store.SirenRecom)
    },
    // 修改当前播放对象
    gaiMusic(store,data){
         let a = {}
         a              = data;
         a.num          = 0;
         a.duration     = 0;
         a.timeDisplay  = 0;
         a.currentTimes = '';
         a.myAudio      = null;
         store.song     = a;

         window.sessionStorage.setItem('song',JSON.stringify(store.song))
         console.log('音乐对象',store.song)
    },
    // 修改播放状态
    gaiPaly(store,data){
          store.song.isPlay = data;
          console.log('公共的isPlay',data)
    },
    // 获取dom对象
    gaiAudio(store,data){
      store.song.myAudio = data;
       
      //  将dom元素转为字符串的方法
      function nodeToString ( node ) {
         let tmpNode = document.createElement( "div" );
         tmpNode.appendChild( node.cloneNode( true ) );
         let str = tmpNode.innerHTML;
         tmpNode = node = null; 
         return str;
      }
      let myAudioStr = nodeToString(data);
      store.song.myAudioStr = myAudioStr;

       window.sessionStorage.setItem('song',JSON.stringify(store.song))
      console.log(store.song.myAudio,'音乐播放器')
    },
    // 隐藏播放器
    gaiIsflash(store,data){
      store.song.isFlash = data;
      console.log('公共的isFlash',store.song.isFlash)
    },
    // 改播放队列
    gaiQueue(store,data){
      
      let queue = JSON.parse(sessionStorage.getItem('queue'));
       console.log(queue , data)
      
        console.log(1231)
        sessionStorage.setItem('queue',JSON.stringify(data));
        store.queue = data;
      
      
    },
    // 改变队列中现在播放歌曲的序号
    gaiIndex(store,data){
      console.log('之前的播放序号',data,store.queueIndex)
      store.queueIndex = store.queueIndex + data;
      store.queueIndex = store.queueIndex < 0 ? store.queue.length-1 : store.queueIndex;
      store.queueIndex = store.queueIndex > store.queue.length-1 ? 0  : store.queueIndex;
      
      console.log('公共数据index',data,store.queueIndex)
    }
  },
  actions: {
    gaiPaly({commit,state},data){
      console.log(state.SirenRecom)
      state.SirenRecom.forEach(val=>{
            val.forEach(item=>{
              if(item.id == state.song.id){
                   item.isItemPlay = !data;
              }else{
                   item.isItemPlay = true;
              }
            })
          })
      commit('gaiPaly',data);
    },
    gaiAudio({commit,state},data){
      commit('gaiAudio',data);
      state.song.duration = data.duration;
    },
    gaiIsflash({commit,state},data){
      commit('gaiIsflash',data);
      console.log('公共的isFlash_02',state)
    },
    gaiLyric({state},data){
      state.lyric.lineNo = state.lyric.lineNo + data;
    },
    gaiQueue({commit, dispatch ,state},data){
      commit('gaiQueue',data)
      commit('gaiIsflash',true);
      commit('gaiPaly',true);
      console.log(state.song.isFlash,'这')

      
      return state.queue;
      console.log('公共数据队列',state.queue)
    },
    gaiMusic({commit ,state},data){
      commit('gaiMusic',data)
    
    },
    gaiIndex({commit ,state},data){
      console.log('公共数据index',data)
      commit('gaiIndex',data);
      return state.queue
    }
   



  },
  modules: {
  }
})
