import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import {Skeleton ,RadioGroup, Radio,Sticky,PullRefresh,Search,Slider,Progress, PasswordInput, NumberKeyboard,CountDown, TreeSelect , Divider , Loading , Dialog ,Tag , Uploader,Overlay ,AddressList,Area ,AddressEdit ,Switch,SwitchCell ,ShareSheet , Grid, GridItem ,SubmitBar ,Checkbox, CheckboxGroup, SwipeCell , Stepper , Button,NavBar,Icon, Image as VanImage , Form ,Field , Popup ,Cell, CellGroup ,Toast  ,Tabbar, TabbarItem ,Swipe, SwipeItem , Lazyload ,Tab, Tabs , Card , GoodsAction, GoodsActionIcon, GoodsActionButton } from 'vant';
import 'lib-flexible/flexible'
import axios from 'axios'
import VueAxios from 'vue-axios'


Vue
   .use(Button)
   .use(NavBar)
   .use(Icon)
   .use(VanImage)
   .use(Form)
   .use(Field)
   .use(Popup)
   .use(Cell, CellGroup)
   .use(Toast)
   .use(Tabbar)
   .use(TabbarItem)
   .use(Swipe)
   .use(SwipeItem)
   .use(Lazyload)
   .use(Tab)
   .use(Tabs)
   .use(Card)
   .use(GoodsAction)
   .use(GoodsActionButton)
   .use(GoodsActionIcon)
   .use(Stepper)
   .use(SwipeCell)
   .use(Checkbox)
   .use(CheckboxGroup)
   .use(SubmitBar)
   .use( Grid)
   .use(GridItem)
   .use(ShareSheet)
   .use(SwitchCell)
   .use(Switch)
   .use(AddressEdit)
   .use(Area)
   .use(AddressList)
   .use(Overlay)
   .use(Uploader)
   .use(Tag)
   .use(Dialog)
   .use(Loading)
   .use(Divider)
   .use(TreeSelect)
   .use(CountDown)
   .use(PasswordInput)
   .use(NumberKeyboard)
   .use(Progress)
   .use(Slider)
   .use(Search)
   .use(PullRefresh)
   .use(Sticky)
   .use(RadioGroup)
   .use(Radio)
   .use(Skeleton)


   // axios拦截器，在发起请求之前执行
axios.interceptors.request.use(config => {

//   //  处理post请求参数，进行参数序列化
  if(config.method == 'post'){
    //  
    // 序列化post请求参数，进行参数系列化
    let paramsString =  '';
    for(let key in config.data){
      
      paramsString += `${key}=${config.data[key]}&`
    
    }
    config.data = paramsString.slice(0,-1)
  }
  return config;
})
  


axios.defaults.baseURL = 'http://127.0.0.1:3000'

Vue.prototype.useId = window.localStorage.getItem('useId')

Vue.config.productionTip = false

Vue.use(VueAxios, axios);

Vue.prototype.$store = store.state;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
