<template>
    <div class="myFlash" v-show="music.isFlash">
       
            <img :src="music.imgUrl" @click="goPages('Song',music.id)"/>
            <div class="delis">
                <p>{{music.name}}</p>
                <p>相信 这就是音乐的力量</p>
            </div>

            <span class="iconfont" :class="music.isPlay ? 'icon-pause' : 'icon-bofangjian' " @click="control"></span>
            <van-cell  @click="showPopup">
                <template>
                    <span class="iconfont icon-bofangliebiao"></span>
                </template>
            </van-cell>
            <audio ref="myAudio" :src="music.url"></audio>


            <!-- 队列弹窗 -->
            <van-popup v-model="show">
                <van-icon name="cross" @click="ff_show"/>
                <div class="bodys" >
                    <ul>
                        <li  class="danSong" v-for="(Item,index) in newQueue" :key="index">
                            <span>{{index-0+1}}</span>
                            <div class="gequ" @click="ff_queueIndex(index)">
                                <p>{{Item.songName}}</p>
                                <p>
                                    <span>独家</span>
                                    <span>{{Item.autName}}</span>
                                    <span>-{{Item.songBrief}}</span>
                                </p>
                            </div>
                            <span class="iconfont icon-iconfontlajitong" @click="rovemItem(index,Item.songName)"></span>
                        </li>
                    </ul>
                </div>
            </van-popup>
    </div>

    
</template>

<script>

    export default {
        name : 'Flash',
        data() {
            return {
                // type   : '',
                icons     : 'icon-bofangjian',
                duration  : 0,
                isCanplay : true,
                show      : false,
                newQueue  : [],
            }
        },
        created() {
            console.log('这',JSON.parse(sessionStorage.getItem('queue')))
            this.newQueue = JSON.parse(sessionStorage.getItem('queue'));    
        },
        methods:{
            // 开关
            control(){
                  this.ff_isPlay(!this.music.isPlay);
                  console.log(this.music.isPlay ,"Flash")
                  if(this.music.isPlay){
                        this.play();     
                  }else{
                        this.pause();
                  }  
            },
            // 播放音乐
            play(){
                this.$refs.myAudio.play();
                
            },
            // 暂停音乐
            pause(){
                this.$refs.myAudio.pause();
               
            },
            goPages(name,id){
                this.show = false;
                this.$router.push({name,query:{id}});
            },
            // 修改按钮状态
            ff_isPlay(data){
                this.$store.dispatch('gaiPaly',data)
            },
            // 传递音频标签
            ff_myAudio(data){
                this.$store.dispatch('gaiAudio',data)
            },
            // 修改播放队列的序号
            ff_queueIndex(data){
                console.log('flash中的序号',data)
                this.$store.dispatch('gaiIndex',data).then(res=>{
                   console.log(res[this.queueIndex])
                   if(res[this.queueIndex]){
                       this.ff_songs(res[this.queueIndex].songId,res[this.queueIndex].songName,res[this.queueIndex].songUrl); 
                   }
                   
                });
            },
             // 获取歌曲url
            ff_songs(id,name,imgUrl){
                 this.axios({
                     url : '/song/url',
                     method : 'GET',
                     params : {
                         id
                     }
                 })
                 .then(res=>{
                     console.log(res)
                     if(res.data.code == 200){
                           let a = {};
                          
                           a.name     = name;
                           a.url      = res.data.data[0].url;
                           a.imgUrl   = imgUrl;
                           a.id       = id;
                           a.isFlash  = true;
                           a.isPlay   = true;
                           this.musicObj = a;
                           this.$store.dispatch('gaiMusic',this.musicObj);
                           
                     }
                 })
                 .catch(err=>{
                     console.log(err)
                 })
            },
            // 显示
            showPopup() {
               
                        if(this.newQueue){
                            this.show = true;
                            let queue = JSON.parse(sessionStorage.getItem('queue'));
                        if(queue){
                            this.newQueue = this.queue;
                        }

                        }else{
                            this.$toast('请添加歌曲到播放队列')
                        }
            },
            ff_show(){
                this.show = false;
            },
            ff_a(){
                this.control();
                this.ff_queueIndex(1);
            },
             // 删除队列的一项
            rovemItem(data,songName){
                this.$dialog.confirm({
                        message: `确认将“${songName}”移除`,
                    })
                    .then(() => {
                        let quese =  this.queue.splice(data,1);
                    
                        window.sessionStorage.setItem('queue',JSON.stringify(this.queue))
                        

                        this.$toast('移除成功')
                        if(this.queue.length <= 0){
                                this.show = false;
                        }
                    })
                    .catch(() => {
                        // on cancel
                    });
                
            }
          


            
        },
        computed: {
            music(){
                     return this.$store.state.song; 
            },
            queue(){
                     return this.$store.state.queue; 
            },
            queueIndex(){
                     return this.$store.state.queueIndex; 
            },
           
        },
        watch: {
            music(news,old){
                  news.num++
                  if(news.num == 1){                       
                       let time02 = function(){
                            return new Promise((resloe,reject)=>{
                                   resloe();
                           })
                       }
                       Promise.all([time02])
                       .then(res=>{
                           console.log(111)
                            
                            // 获取歌曲加载完毕 
                            this.$refs.myAudio.oncanplay = ()=>{
                                
                                console.log('121')
                                this.ff_myAudio(this.$refs.myAudio);
                                this.play();
                                
                            }

                           setTimeout(()=>{
                                this.$refs.myAudio.removeEventListener("ended", this.ff_a)
                                this.$refs.myAudio.addEventListener("ended", this.ff_a)
                            },1000)
                        
                           
                            
                            news.num = 0;
                       })

                  };
                  console.log('false中的music对象',news)
                  if(news.isFlash){
                      console.log('出现了',news.isFlash)
                  }
                 
            },
            // bug
            // 歌词滚动那停止 再切换为显示false中 没更新到对应的值
            queue(val){
                  this.newQueue = val
            },

           

           
        },
    }
</script>

<style lang="less" scoped>
       .myFlash{
           width: 100%;
           height: 60px;
           background-color: #fff;
           position: fixed;
           display: flex;
           bottom: 0px;
           z-index: 100;
           align-items: center;
           justify-content: space-around;
           box-shadow: 0px 0px 16px 0px #ddd;

           img{
               display: inline-block;
               flex: 0 0 50px;
               height: 50px;
               border-radius: 50%;
               display: inline-block;
               margin-left: 20px;
               
           }
           >div{
               display: inline-block;
               flex: 1;
               margin-left: 10px;
               text-align: left;
               
           }
           >span{
               display: inline-block;
               flex: 0 0 50px;
               font-size: 23px;
           }
           >span:nth-child(3){
               font-size: 40px;
               position: relative;
               top: -3px;
           }
            >span:nth-child(4){
               margin-right: 10px;
           }
           p{
               margin: 3px 0px;
           }
           .delis{
               p:nth-child(1){
                   display: inline-block;
                   width: 180px;
                   font-size: 13px;
                   font-weight: 550;
                   white-space: nowrap;
                   text-overflow: ellipsis;
                   overflow: hidden;

               }
               p:nth-child(2){
                   color: #a3a3a3;
                   font-size: 12px;
                   transform: scale(.9);
                   position: relative;
                   left: -9px;
               }
           }
       }
       /deep/.van-popup--center{
            width: 80%;
            height: 400px;
            border-radius: 15px;
            margin: 30px 0 !important;

            /deep/.van-icon{
                    font-size: 20px;
                    position: relative;
                    left: 90%;
                    top: 8px;

            }
       }
       .van-cell__value--alone{
            font-size: 30px;
            position: relative;
            left: -4px;
            top: -2px;


            .iconfont{
                font-size: 25px;
            }


           
       }
         .bodys{
            height: calc(100% - 50px);
            width: 100%;
            // background-color: lightcoral;
            overflow-y: scroll;

            ul{
                width: 95%;
                margin: 0px auto;
               
            }
        }
            .danSong{
                display: flex;
                margin: 5px 0px;

                p{
                    margin: 5px 0px 3px;
                    font-size: 13px;
                    text-align: left;
    
                }

                >span{
                    flex: 0 0 25px;
                }
                >div{
                    flex: 1;
                }
                >span:nth-child(1){
                    line-height: 50px;
                    font-size: 15px;
                    font-weight: 550;
                    color: #a2a2a2;
                    text-align: center;
                }
                >span:nth-child(3){
                    line-height: 50px;
                    text-align: center;
                    font-size: 19px;
                    font-weight: 550;
                    color: #a2a2a2;
                }
        }
            .gequ{
            overflow: hidden;
            
            >p:nth-child(1){
                font-size: 13px;
                font-weight: 550;
                padding-left: 4px;
                box-sizing: border-box;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;

            }
            >p:nth-child(2){
                width: 100%;
                white-space: nowrap;


                span:nth-child(1){
                    display: inline-block;
                    font-size: 12px;
                    border: 1px solid #fe3a3c;
                    transform: scale(0.7);
                    color: #fe3a3c;
                    padding: 0px 3px;
                }
                span:nth-child(3){
                    color: #a2a2a2;
                    font-size: 12px;
                    margin-left: 10px;
                    width: 100px;
                    display: inline-block;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    position: relative;
                    top: 4px;
                }

            }
            
        }
           
</style>